  font-weight: bold;
}

/* Tables
   ========================================================================== */
/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.cssloading__overlay {
  z-index: -2;
  opacity: 0;
  width: 0;
  height: 0;
  visibility: hidden;
}

body {
  font-size: 16px;
  line-height: 1.4;
  font-family: Arial, Helvetica, sans-serif;
  color: #263D55;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.fonts-loading body {
  font-family: Arial, Helvetica, sans-serif;
}

a {
  color: #5400ff;
  text-decoration: none;
}

a:hover, a:active, a:focus {
  color: #5400ff;
}

/* Headings
----------------------------------------------------------------------------------------------------*/
h1 {
  line-height: 1.2;
  font-size: 25px;
}

h2 {
  line-height: 1.2;
  font-size: 23px;
}

h3 {
  line-height: 1.2;
  font-size: 21px;
}

h4 {
  line-height: 1.2;
  font-size: 19px;
}

h5 {
  line-height: 1.2;
  font-size: 17px;
}

h6 {
  line-height: 1.2;
  font-size: 15px;
}

/* Lists
----------------------------------------------------------------------------------------------------*/
ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  margin: 0;
}

/* Spacing
----------------------------------------------------------------------------------------------------*/
p,
dl,
hr,
h1,
h2,
h3,
h4,
h5,
h6,
ol,
ul,
pre,
table,
address,
fieldset,
figure {
  margin: 0 0 20px 0;
}

/* Wysiwyg style
----------------------------------------------------------------------------------------------------*/
hr {
  clear: both;
  border: 0;
  height: 1px;
  background: #65778A;
}

blockquote {
  border-left: 5px solid #65778A;
  padding: 20px;
  margin: 0 0 20px 0;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
}

blockquote p:last-child {
  margin: 0;
}

cite {
  font-style: italic;
}

small {
  font-size: 11px;
}

table {
  margin: 20px 0;
  width: 100%;
  border: 1px solid #ddd;
  border-collapse: collapse;
  border-left: 0;
  border-radius: 3px;
}

table th {
  padding: 10px;
  border-left: 1px solid #ddd;
  border-top: 0;
  vertical-align: top;
  text-align: left;
}

table td {
  padding: 10px;
  border-top: 1px solid #ddd;
  border-left: 1px solid #ddd;
  vertical-align: top;
  text-align: left;
}

address {
  display: block;
  margin-bottom: 20px;
  font-style: normal;
  line-height: 20px;
}

abbr {
  text-decoration: underline;
}

abbr[title],
abbr[data-original-title] {
  cursor: help;
  border-bottom: 1px dotted #ddd;
}

big {
  font-size: larger;
}

code {
  display: inline-block;
  padding: 2px 5px;
  background-color: #65778A;
  border: 1px solid #000;
  font-family: monospace;
  font-size: 16px;
  border-radius: 3px;
}

s,
strike,
del {
  text-decoration: line-through;
}

em,
var {
  font-style: italic;
}

ins {
  background-color: #65778A;
  text-decoration: none;
}

tt,
kbd,
samp {
  font-family: monospace;
}

pre {
  display: block;
  padding: 10px;
  margin: 0 0 20px;
  overflow-wrap: break-all;
  word-wrap: break-all;
  word-break: break-all;
  white-space: pre-wrap;
  background-color: #65778A;
  border: 1px solid #000;
  border-radius: 3px;
}

q {
  display: inline;
  font-style: italic;
}

sub,
sup {
  bottom: -0.25em;
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

/* Main Layout
----------------------------------------------------------------------------------------------------*/
.master-container {
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
}

.container {
  position: relative;
  max-width: 1024px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 10px;
}

.container:after {
  content: " ";
  display: block;
  clear: both;
}

@media screen and (min-width: 64em) {
  .container {
    padding: 0;
  }
  .container.posts-list {
    max-width: 880px;
    margin-left: auto;
    margin-right: auto;
  }
  .container.posts-list:after {
    content: " ";
    display: block;
    clear: both;
  }
}

@media screen and (min-width: 64em) {
  .content {
    width: 74.46809%;
    float: left;
    margin-right: 2.12766%;
  }
}

@media screen and (min-width: 64em) {
  .sidebar {
    width: 23.40426%;
    float: right;
    margin-right: 0;
  }
}

@media screen and (min-width: 64em) {
  .header {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    border-left: 2px solid #5400ff;
  }
}

.header__logo {
  display: block;
}

@media screen and (min-width: 64em) {
  .header__logo {
    width: 246px;
    height: auto;
  }
}
